<template>
  <div id="signup">
    <div class="errorMsg">{{message}}</div>
    <h1><strong>Signup</strong> </h1>
    <div>
      <label for="username"></label>
      <input type="text" id="username" v-model="credentials.username" placeholder="USERNAME" > 
      <button @click="checkUsername(credentials.username)"></button>
    </div>
    <div>
      <label for="email"></label>
      <input type="email" id="email" v-model="credentials.email" placeholder="EMAIL" >
    </div>
    <div>
      <label for="password"></label>
      <input type="password" id="password1" v-model="credentials.password1" placeholder="PASSWORD" >
    </div>
    <div>
      <label for="passwordConfirmation"></label>
      <input type="password" id="password2" v-model="credentials.password2" placeholder="PASSWORD CHECK" >
    </div>
    <button @click="signup"></button>
  </div>
</template>

<script>
import axios from 'axios'

// const SERVER_URL = process.env.VUE_APP_SERVER_URL

export default {
  name: 'Signup',
  data: function () {
    return {
      message: '',
      ckUsername: false,
      credentials : {
        username : null,
        email : null,
        password1 : null,
        password2 : null,
      }
    }
  },
  methods: {
    checkUsername: function (username) {
      if(username != null) {
        axios ({
          method : 'post',
          url : 'http://127.0.0.1:8000/ckeck/username',
          data: username,
        })
          .then(res => {
            if (res.data) {
              this.message = " ."
              this.ckUsername = true
            } else {
              this.message = "  ."
              this.ckUsername = false
            }
          })
          .catch(err => {
            console.log(err);
            this.message = ")   ."
          })
      } else {
        this.message = " ) USERNAME .";
      }
    },
    signup: function () {
      if (this.credentials.username != '' && this.credentials.email != '' &&
            this.credentials.password1 != '' && this.credentials.password2 != ''){
        if (this.ckUsername == true) {
          axios ({
            method : 'post',
            url : 'http://127.0.0.1:8000/rest-auth/signup/',
            data: this.credentials,
          })
            .then(res => {
              console.log(res.status, 'Success Signup')
              //     
              this.$router.push({ name : 'Signin'})
            })
            .catch(err => {
              console.log(err);
              this.message = ")  ."
            })
        } else {
          alert('ID   .')
        }
      } else {
        alert('  .')
      }
    }
  }
}
</script>

<style scoped>
#signup{
    display: flex;
    flex-flow: column;
    justify-content : center;
    align-items: center;
    height:100%;
    padding-bottom: 10em;
}
.errorMsg{
    font-size: 0.8em;
    margin-bottom: 2em;
    color:salmon;
}
#signup > h1 {
    margin-bottom: 1em;
}
input[type="text"],
input[type="password"],
input[type="email"]{
text-align:center;
margin-bottom: 0.6em;
}
#signup > button {
    background-color: rgb(85, 85, 85);
    border:gray;
    color:white;
    padding: 0.4em 0.6em;
    border-radius: 0.4em;
    margin: 0.8em 0 1.5em 0;
}
</style>